<template>
    <div class="container">
        <button type="button" class="btn btn-primary">{{ msg }}</button>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    name: 'Ping',
    data() {
        return {
            msg: '',
        };
    },
    methods: {
        getMessage() {
            const path = 'http://localhost:5000/ping';
            axios
                .get(path)
                .then((res) => {
                    this.msg = res.data;
                })
                .catch((err) => {
                    // eslint-disable-next-line
          console.log(err);
                });
        },
    },
    created() {
        this.getMessage();
    },
};
</script>
